    padding: 0;
    margin: 0;
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 8fr;
}

header {
    padding: 10px;
    grid-column: 1 / 5;
    grid-row: 1 / 2;
    display: flex;
    align-items: center;
    gap: 10px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 2.5rem;
    background-color: #1F2937;
    color: white;
}

.Sidebar {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    display: flex;
    flex-direction: column;
    background-color: #22356F;
    padding: 10px;
}

.Sidebar > p {
    width: 100%;
    font-size: 1.7rem;
    font-family: Verdana, Geneva, Tahoma, sans-sertrif;
    border-bottom: 2px solid grey;
    box-sizing: border-box;
    color: white;
}

.Viewer {
    grid-column: 2 / 5;
    grid-row: 2 / 3;
    border: 2px solid green;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Project {
    display: flex;
    gap: 5px;
    padding: 3px;
    margin-top: 20px;
    align-items: center;
    background-color: white;
    color: 	#2E3A59;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    border-radius: 5px;
    font: 1rem;
}

button {
    width: 50px;
    height: 50px;
    padding: 0%;
    margin: 0%;
    background: white;
    border-radius: 5px;
}

button svg {
    display: block;
}

.AddProject {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: white;
    background-color: #334155;
    font-size: 1rem;
    display: flex;
    gap: 10px;
    align-items: center;
    border-radius: 5px;
}

.ViewerProjectTitle {
    width: 80%;
    margin-top: 50px;
    margin-bottom: 10px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 2rem;
    border-bottom: 2px solid grey;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.Task {
    display: flex;
    flex-direction: column;
    margin: 10px;
    display: flex;
    gap: 5px;
    width: 80%;
    justify-content: flex-start;
}

.Task_title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    width: 100%;
    height: 30px;
    border-radius: 5px;
    padding: 5px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: white;
}

.Exposed {
    display: flex;
    gap: 5px;
}

.Completed {
    background-color: #2ECC71;
}

.Urgent {
    background-color: #FF4C4C;
}

.Medium {
    background-color: #FFA500;
}

.Low {
    background-color: #42A5F5;
}

.Hidden {
    padding: 10px;
    display: none;
    background-color: rgb(237, 245, 248);
    border-radius: 5px;
}

/*must have both task and active class as parent of the hidden class*/
.Task.active .Hidden {
    display: block;
}

.addTask {
    width: 80px;
    height: 80px;
    background-color: #ffffff;
    border: none;
}

.hiddenTop, .hiddenBottom{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.radio {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
}

#addTaskDialog {
    font-size: 1rem;
}

.addTaskName {
    display: flex;
    gap: 5px;
}

.buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,UAAU;IACV,SAAS;IACT,aAAa;IACb,aAAa;IACb,sCAAsC;IACtC,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,eAAe;IACf,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,gDAAgD;IAChD,iBAAiB;IACjB,yBAAyB;IACzB,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,aAAa;IACb,sBAAsB;IACtB,yBAAyB;IACzB,aAAa;AACjB;;AAEA;IACI,WAAW;IACX,iBAAiB;IACjB,kDAAkD;IAClD,6BAA6B;IAC7B,sBAAsB;IACtB,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,uBAAuB;IACvB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,QAAQ;IACR,YAAY;IACZ,gBAAgB;IAChB,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;IACf,gDAAgD;IAChD,kBAAkB;IAClB,UAAU;AACd;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,WAAW;IACX,UAAU;IACV,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,gDAAgD;IAChD,YAAY;IACZ,yBAAyB;IACzB,eAAe;IACf,aAAa;IACb,SAAS;IACT,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,UAAU;IACV,gBAAgB;IAChB,mBAAmB;IACnB,gDAAgD;IAChD,eAAe;IACf,6BAA6B;IAC7B,aAAa;IACb,8BAA8B;IAC9B,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,YAAY;IACZ,aAAa;IACb,QAAQ;IACR,UAAU;IACV,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,sBAAsB;IACtB,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,YAAY;IACZ,gDAAgD;IAChD,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,QAAQ;AACZ;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,aAAa;IACb,oCAAoC;IACpC,kBAAkB;AACtB;;AAEA,qEAAqE;AACrE;IACI,cAAc;AAClB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,yBAAyB;IACzB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;AAClC","sourcesContent":["body {\n    padding: 0;\n    margin: 0;\n    height: 100vh;\n    display: grid;\n    grid-template-columns: 1fr 1fr 1fr 1fr;\n    grid-template-rows: 1fr 8fr;\n}\n\nheader {\n    padding: 10px;\n    grid-column: 1 / 5;\n    grid-row: 1 / 2;\n    display: flex;\n    align-items: center;\n    gap: 10px;\n  